
import Vue from "vue";
import LangSwitcher from "@/components/settings/LangSwitcher.vue";
import startCase from "lodash/fp/startCase";
import Profile from "@/components/navigation/Profile.vue";
import Notifications from "@/components/navigation/Notifications.vue";
import UsersOnline from "@/components/navigation/UsersOnline.vue";
import Currency from "@/components/viewers/Currency.vue";

export default Vue.extend({
  name: "NavBar",

  components: { Currency, Notifications, Profile, LangSwitcher, UsersOnline },

  data: () => ({
    startCase,
    closeOnContentClick: true as boolean,
    showSearch: false as boolean,
    show: false as boolean,
    colors: {
      admin: "red",
      guest: "blue",
      chairman: "orange",
      vice_president: "pink",
      department_head: "green",
      specialist: "brown",
      direction_head: "#3f8dc3",
      branch_head: "aqua",
      bookkeeper_chief: "chartreuse",
      bookkeeper: "crimson",
      expert_chief: "darkturquoise",
      expert: "deepskyblue",
      specialist_ata: "khaki",
      specialist_aaij: "lightgreen",
      beneficiary: "mediumslateblue",
      default: "grey"
    } as any,
    roles: [] as Array<any>
  }),

  watch: {
    user: {
      immediate: true,
      handler() {
        if (this.user) {
          this.roles = this.user.roles.map((item: any) => ({
            ...item,
            color: this.colors[item.alias] || this.colors.default
          }));
        }
      }
    }
  },

  computed: {
    badgeColor() {
      if (this.currentRole) {
        return this.colors[this.currentRole.alias];
      }
      return this.colors.default;
    },
    moduleName() {
      const localizedTitle = this.$t(
        `${this.$route.meta.module}.title`
      ) as string;

      if (localizedTitle.includes(".")) {
        return this.startCase(this.$route.meta.module);
      }
      return localizedTitle;
    },
    currentRole() {
      return this.$store.getters["user/currentRole"];
    },
    user() {
      return this.$store.getters["authentication/credentials"]?.user;
    }
  },

  async mounted() {
    const role = localStorage.getItem("localRole");
    if (
      role &&
      this.user.roles.some((item: any) => item.alias === JSON.parse(role).alias)
    ) {
      await this.$store.dispatch("user/setRole", JSON.parse(role));
    } else if (this.user.roles) {
      await this.$store.dispatch("user/setRole", this.user.roles[0]);
    }
  },

  methods: {
    toggleSideBar(): void {
      this.$store.dispatch("navigation/toggle");
    },
    changeRole(value: any): void {
      this.$store.dispatch("user/changeCurrentRole", value);
    },
    toggleSearch(): void {
      this.showSearch = !this.showSearch;

      if (this.showSearch) {
        this.$nextTick(() => {
          (this.$refs.search as any).focus();
        });
      }
    }
  }
});
